<template>
    <div class="is-app">
        <div class="guide">点击右上角，选择在浏览器中打开</div>
        <img class="logo" src="../assets/images/logo.png" alt="">
        <div class="download-app" @click="downloadApp">下载饭糖</div>
    </div>
</template>

<script>
  export default {
    name: "isApp",
    mounted() {
      window.location.href = 'sweetfans://login';
      this.systemInfo();

    },
    methods: {
      systemInfo: function () {
        const OS = window.navigator.userAgent.toLowerCase();
        let android = OS.indexOf('android') > -1;
        let iphone = OS.indexOf('iphone os') > -1;
        let mac = OS.indexOf('mac os x') > -1;
        let windows = OS.indexOf('windows') > -1;
        let weixin = OS.indexOf('micromessenger') > -1;
        let weibo = OS.indexOf('weibo') > -1;
        let qq = OS.indexOf('qq/') > -1;
        let qzone = OS.indexOf('qzone/') > -1;

        // 安卓
        if (android) {
          if (qq) {
            console.log('android > QQ')
          }
          if (weixin) {
            console.log('android > weixin')
          }
          if (weibo) {
            console.log('android > weibo')
          }
          if (qzone) {
            console.log('android > qzone')
          }
        }
        // iphone
        if (iphone) {
          if (qq) {
            console.log('ios > QQ')
          }
          if (weixin) {
            console.log('ios > weixin')
          }
          if (weibo) {
            console.log('ios > weibo')
          }
          if (qzone) {
            console.log('ios > qzone')
          }
        }
        // 电脑
        if (this.isPC()) {
          console.log('PC');
          if (mac > -1) {
            window.location.href = 'https://a.app.qq.com/o/simple.jsp?pkgname=com.sweetfans.app';
          } else if (windows > -1) { //iphone
            window.location.href = 'sweetfans://login';
            window.location.href = 'https://apps.apple.com/cn/app/%E9%A5%AD%E7%B3%96/id1135608630';//iosAppStore
          }
        }
      },
      //判断是否为pc
      isPC: function () {
        const OS = window.navigator.userAgent.toLowerCase();
        let Agents = ["android", "iphone", "symbianos", "windows phone", "ipad", "ipod"];
        let flag = true;
        for (let v = 0; v < Agents.length; v++) {
          if (OS.indexOf(Agents[v]) > 0) {
            flag = false;
            break;
          }
        }
        return flag;
      },
      downloadApp: function () {
        const OS = window.navigator.userAgent.toLowerCase();
        let android = OS.indexOf('android') > -1;
        let iphone = OS.indexOf('iphone os') > -1;
        let weibo = OS.indexOf('weibo') > -1;
        // 移动设备中
        if (android) { //安卓
          window.location.href = 'sweetfans://login';
          if (weibo) {
            return false;
          } else {
            setTimeout(function () {
              window.location.href = 'https://a.app.qq.com/o/simple.jsp?pkgname=com.sweetfans.app';
            }, 10)
          }
        } else if (iphone) { //iphone
          window.location.href = 'sweetfans://login';
          setTimeout(function () {
            window.location.href = 'https://apps.apple.com/cn/app/%E9%A5%AD%E7%B3%96/id1135608630';//iosAppStore
          }, 10);
        }
      }
    }
  }
</script>

<style scoped>
    .is-app {
        height: 100vh;
        width: 100vw;
        position: fixed;
        z-index: 5;
        left: 0;
        top: 0;
        background: url(../assets/images/openBrowser.png) center no-repeat;
        background-size: 100% 100%;
    }

    .guide {
        height: .48rem;
        width: 77%;
        color: #909090;
        font-size: .3rem;
        line-height: .78rem;
        margin: 1rem auto;
        background: url(../assets/images/guide.png) right no-repeat;
        background-size: .91rem .48rem;

    }

    .logo {
        height: 1.77rem;
        width: 2rem;
        display: block;
        margin: 2rem auto;
    }

    .download-app {
        height: .7rem;
        width: 2.3rem;
        color: #EB38F3;
        text-align: center;
        font-size: .3rem;
        line-height: .7rem;
        border: .04rem solid #EB38F3;
        border-radius: .35rem;
        position: fixed;
        left: 0;
        right: 0;
        bottom: 2rem;
        margin: auto;
    }
</style>
