<template>
    <div class="home-page">
        <van-popup v-model="showPopup" position="bottom" :overlay="true">
            <van-cell v-for="(n,index) in list" :title="n" :key="index" @click="tipOffs(n)"></van-cell>
        </van-popup>
        <div class="header-box">
            <div class="blur-bg-box">
                <div class="blur-bg"
                     :style="userHeader">
                </div>
            </div>
            <header>
                <div class="swf-header">
                    <img class="header-img" :src="userInfo.headUrl" alt="">
                </div>
                <div class="swf-nickname">{{userInfo.account}}</div>
                <ul class="vip-privilege">
                    <li class="iconfont"
                        :class="{'icon-xingbie-nan':userInfo.sex===1,'icon-xingbie-nv':userInfo.sex===0}"></li>
                    <li class="iconfont icon-tianchongxing-" v-show="userInfo.vipStatus===1"></li>
                    <!--<li></li>-->
                </ul>
                <div class="swf-button">
                    <!--<span class="buttons btn-edit">编辑</span>-->
                    <!--<span class="buttons btn-message">私信</span>-->
                    <!--<span class="buttons btn-follow" v-show="userInfo.isConcern===0">关注</span>-->
                </div>
                <p class="specific-sign">个性签名：{{userInfo.autograph?userInfo.autograph:'这个人很懒，没有个性签名 ~ '}}</p>
            </header>
        </div>
        <div class="white-box swf-handle">
            <span class="swf-num">
                <b class="text-center">关注</b>
                <i>{{userInfo.followNum}}</i>
            </span>
            <span class="swf-num">
                <b class="text-center">粉丝</b>
                    <i>{{userInfo.fansNum}}</i>
            </span>
            <span class="swf-num">
                <b class="text-center">获赞</b>
                <i>{{likenum}}</i>
            </span>
        </div>
        <div @click="$isApp" class="white-box your-follow-ido">
            <span class="follow-ido-t">关注爱豆</span>
            <div class="three-ido">
                <img v-for="(item,index) in threeIdol" :src="item.headsurl" :alt="item" :key="index">
            </div>
            <div class="arrow-right"></div>
        </div>
        <div class="home-page-tab">
            <div v-if="userInfo.userType===0">
                <default-page v-if="myMoments.length===0"
                              :img-url="require('../../assets/images/default-data.png')"></default-page>
                <template v-else v-for="(item,index) in myMoments">
                    <!-- 饭圈动态 -->
                    <div @click.stop="$isApp" class="item-sweetfans" :key="index">
                        <div class="swf-head-img"
                             :style="{background:'url('+item.userHead+')center no-repeat',backgroundSize:'cover'}"></div>
                        <div class="sweetfans-top">
                            <div class="swf-nickname">{{item.account}}</div>
                            <div class='swf-date'>{{$formatTime(new Date(item.createTime))}}</div>
                            <!--<div class='follow{{item.isFollow===false?0:1}}' data-index='{{index}}' @click='follow'>-->
                            <!--{{item.isFollow===false?'关注':'已关注'}}-->
                            <!--</div>-->
                        </div>
                        <div class='about-sweetfans'>
                            <!-- 饭圈标题及文字 -->
                            <div class='swf-title-text' v-html="item.title"></div>
                            <!--<div class='swf-content-text'>{{item.swfContent}}</div>-->
                            <!-- 一张纯图片 -->
                            <div class='swf-img-i1' v-if="item.type===1||item.type===0">
                                <template v-for="(imgItem,index) in item.url.slice(0,1)">
                                    <div class='row-i1'
                                         :key="index"
                                         :data-moments-img='item.url.slice(0,1)'
                                         :data-pic-index='index'
                                         @click.stop='viewPicture(index,item.url.slice(0,1))'
                                         :style="{background:'url('+imgItem+')center no-repeat','background-size':'cover'}"
                                    ></div>
                                </template>
                            </div>
                            <!-- 2/4张纯图片 -->
                            <div class='swf-img-i2'
                                 v-else-if="((item.url.length===2)||(item.url.length===4))&&(item.type===2)">
                                <template v-for="(imgItem,index) in item.url">
                                    <div class='row-i2'
                                         :key="index"
                                         :data-moments-img='item.url'
                                         :data-pic-index='index'
                                         @click.stop='viewPicture(index,item.url)'
                                         :style="{background:'url('+imgItem+')center no-repeat','background-size':'cover'}"
                                    ></div>
                                </template>
                            </div>
                            <!-- 3/5/6/7/8/9张纯图片 -->
                            <div class='swf-img-i3'
                                 v-else-if="((item.url.length===3)||(item.url.length>3))&&(item.type===2)">
                                <template v-for="(imgItem,index) in item.url">
                                    <div class='row-i3'
                                         :key="index"
                                         :data-moments-img='item.url'
                                         :data-pic-index='index'
                                         @click.stop='viewPicture(index,item.url)'
                                         :style="{background:'url('+imgItem+')center no-repeat','background-size':'cover'}"
                                    ></div>
                                </template>
                            </div>
                            <!-- 一个横视频 -->
                            <div class='swf-v-row row-video' v-if="item.type===3">
                                <video controls :src='item.url[0]'></video>
                                <div class="load-layer" @click.stop>
                                    <div :style="{width:'100%',height:'100%',background:'url('+item.vPosterImg+')center no-repeat','backgroundSize':'cover'}"></div>
                                    <i class="player-icon icon-preview iconfont icon-bofang"
                                       @click.stop="playVideo(index)"></i>
                                </div>
                            </div>
                            <!-- 一个竖视频 -->
                            <div class='swf-v-row column-video' v-if="item.type===4">
                                <video controls :src='item.url[0]'></video>
                                <div class="load-layer" @click.stop>
                                    <div :style="{width:'100%',height:'100%',background:'url('+item.vPosterImg+')center no-repeat','backgroundSize':'cover'}"></div>
                                    <i class="player-icon icon-preview iconfont icon-bofang"
                                       @click.stop="playVideo(index,$event)"></i>
                                </div>
                            </div>
                            <div class="share-link" v-if="item.type===7">
                                <img class="share-img" :src="item.url[0]" alt="">
                                <div class="share-text">{{item.url[1]}}</div>
                            </div>
                        </div>
                        <div class='swf-mark' v-if="item.towntalkTitle">#{{item.towntalkTitle}}#
                            <!--<div class='hot-icon'>热</div>-->
                        </div>
                        <div class='swf-address' v-if="item.location">
                            <div class='iconfont icon-weizhi icon-addr'></div>
                            <div class='addr-text'>{{item.location[0]+'·'+item.location[1]}}</div>
                        </div>
                        <!-- 饭圈操作 -->
                        <div class="comment-operate">
                            <div class="operate operate-point" @click="tipOffs">···</div>
                            <div class="operate operate-like">{{item.collectNum}}</div>
                            <div class="operate operate-comment">{{item.commentNum}}</div>
                            <div class="operate operate-trans">{{item.forwardingId}}</div>
                        </div>
                    </div>
                </template>
            </div>
            <van-tabs sticky v-if="userInfo.userType===1" v-model="active" @click="changeActive" color="#FF2E4D"
                      :lineWidth=20
                      swipeable>
                <van-tab :title="tabName[0]">
                    <div class="tab-content">
                        <div class="moments-box">
                            <default-page v-if="myMoments.length===0"
                                          :img-url="require('../../assets/images/default-data.png')"></default-page>
                            <template v-for="(item,index) in myMoments">
                                <!-- 饭圈动态 -->
                                <div @click.stop="$isApp" class="item-sweetfans" :key="index">
                                    <div class="swf-head-img"
                                         :style="{background:'url('+item.userHead+')center no-repeat',backgroundSize:'cover'}"></div>
                                    <div class="sweetfans-top">
                                        <div class="swf-nickname">{{item.account}}</div>
                                        <div class='swf-date'>{{$formatTime(new Date(item.createTime))}}</div>
                                        <!--<div class='follow{{item.isFollow===false?0:1}}' data-index='{{index}}' @click='follow'>-->
                                        <!--{{item.isFollow===false?'关注':'已关注'}}-->
                                        <!--</div>-->
                                    </div>
                                    <div class='about-sweetfans'>
                                        <!-- 饭圈标题及文字 -->
                                        <div class='swf-title-text' v-html="item.title"></div>
                                        <div class='swf-content-text'>{{item.swfContent}}</div>
                                        <!-- 一张纯图片 -->
                                        <div class='swf-img-i1' v-if="item.type===1||item.type===0">
                                            <template v-for="(imgItem,index) in item.url">
                                                <div class='row-i1'
                                                     :key="index"
                                                     :data-moments-img='item.url'
                                                     :data-pic-index='index'
                                                     @click.stop='viewPicture(index,item.url)'
                                                     :style="{background:'url('+imgItem+')center no-repeat','background-size':'cover'}"
                                                ></div>
                                            </template>
                                        </div>
                                        <!-- 2/4张纯图片 -->
                                        <div class='swf-img-i2'
                                             v-else-if="((item.url.length===2)||(item.url.length===4))&&(item.type===2)">
                                            <template v-for="(imgItem,index) in item.url">
                                                <div class='row-i2'
                                                     :key="index"
                                                     :data-moments-img='item.url'
                                                     :data-pic-index='index'
                                                     @click.stop='viewPicture(index,item.url)'
                                                     :style="{background:'url('+imgItem+')center no-repeat','background-size':'cover'}"
                                                ></div>
                                            </template>
                                        </div>
                                        <!-- 3/5/6/7/8/9张纯图片 -->
                                        <div class='swf-img-i3'
                                             v-else-if="((item.url.length===3)||(item.url.length>3))&&(item.type===2)">
                                            <template v-for="(imgItem,index) in item.url">
                                                <div class='row-i3'
                                                     :key="index"
                                                     :data-moments-img='item.url'
                                                     :data-pic-index='index'
                                                     @click.stop='viewPicture(index,item.url)'
                                                     :style="{background:'url('+imgItem+')center no-repeat','background-size':'cover'}"
                                                ></div>
                                            </template>
                                        </div>
                                        <!-- 一个横视频 -->
                                        <div class='swf-v-row row-video' v-if="item.type===3">
                                            <video controls :src='item.url[0]'></video>
                                            <div class="load-layer" @click.stop>
                                                <div :style="{width:'100%',height:'100%',background:'url('+item.vPosterImg+')center no-repeat','backgroundSize':'cover'}"></div>
                                                <i class="player-icon icon-preview iconfont icon-bofang"
                                                   @click.stop="playVideo(index)"></i>
                                            </div>
                                        </div>
                                        <!-- 一个竖视频 -->
                                        <div class='swf-v-row column-video' v-if="item.type===4">
                                            <video controls :src='item.url[0]'></video>
                                            <div class="load-layer" @click.stop>
                                                <div :style="{width:'100%',height:'100%',background:'url('+item.vPosterImg+')center no-repeat','backgroundSize':'cover'}"></div>
                                                <i class="player-icon icon-preview iconfont icon-bofang"
                                                   @click.stop="playVideo(index,$event)"></i>
                                            </div>
                                        </div>
                                        <div class="share-link" v-if="item.type===7">
                                            <img class="share-img" :src="item.url[0]" alt="">
                                            <div class="share-text">{{item.url[1]}}</div>
                                        </div>
                                    </div>
                                    <div class='swf-mark' v-if="item.towntalkTitle">#{{item.towntalkTitle}}#
                                        <!--<div class='hot-icon'>热</div>-->
                                    </div>
                                    <div class='swf-address' v-if="item.location">
                                        <div class='iconfont icon-weizhi icon-addr'></div>
                                        <div class='addr-text'>{{item.location[0]+'·'+item.location[1]}}</div>
                                    </div>
                                    <!-- 饭圈操作 -->
                                    <div class="comment-operate">
                                        <div class="operate operate-point" @click="tipOffs">···</div>
                                        <div class="operate operate-like">{{item.collectNum}}</div>
                                        <div class="operate operate-comment">{{item.commentNum}}</div>
                                        <div class="operate operate-trans">{{item.forwardingId}}</div>
                                    </div>
                                </div>
                            </template>
                        </div>
                    </div>
                </van-tab>
                <van-tab :title="tabName[1]">
                    <div class="tab-content">
                        <div class="support-box">
                            <default-page v-if="support.length===0"
                                          :img-url="require('../../assets/images/default-data.png')"></default-page>
                            <div v-else class="swf-upper" v-for="(item,index) in support.list" @click="$isApp"
                                 :key="index">
                                <div class="upper-logo-img"
                                     :style="{background:'url('+item.imgurl+')center no-repeat',backgroundSize:'cover'}"></div>
                                <p class="upper-describe">{{item.title}}</p>
                                <!--<van-progress :percentage="100" color="#FF2E4D"/>-->
                                <my-progress :progresssVal="Math.round(item.percent)"></my-progress>
                                <div class="progress-text">
                                    <span>{{item.count}}人支持</span>
                                    <span v-if="typeof(item.endtime)==='number'">截止时间：{{$formatTime(new Date(item.endtime))}}</span>
                                    <span v-if="typeof(item.endtime)==='string'">{{item.endtime}}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </van-tab>
                <van-tab :title="tabName[2]">
                    <div class="tab-content">
                        <div class="swf-goods-box">
                            <default-page v-if="goods.length===0"
                                          :img-url="require('../../assets/images/default-data.png')"></default-page>
                            <div v-else class="swf-goods" @click="$isApp" v-for="(item,index) in goods.list"
                                 :key="index">
                                <div class="swf-goods-img"
                                     :style="{background:'url('+item.arrayImgUrl+')center no-repeat',backgroundSize:'cover'}"></div>
                                <p class="swf-goods-price">¥ {{item.price}}</p>
                                <p class="swf-goods-describe">{{item.title}}</p>
                            </div>
                        </div>
                    </div>
                </van-tab>
            </van-tabs>
        </div>
        <div class="app-see-more" @click="$isApp()">进入App查看更多</div>
    </div>
</template>

<script>
  // @ is an alias to /src
  import { ImagePreview } from 'vant'
  import myProgress from '../../components/Progress'
  import isApp from '../../components/IsApp'
  import defaultPage from '../../components/DafultPage'

  export default {
    name: 'homePage',
    components: {
      myProgress,
      isApp,
      defaultPage
    },
    data() {
      return {
        likenum: 0,
        userInfo: {
          id: 1,
          userId: 1,
          account: "",
          headUrl: null,
          fansNum: 0,
          isConcern: 1,
          sex: 1,
          integral: 0,
          todayIntegral: 0,
          autograph: "",
          userType: 0,
          followNum: 0,
          userLvl: 0,
          vipStatus: 0,
          teamId: 0,
          userStatus: 0,
          phoneNum: null,
          birthday: 0,
          weixin: 0,
          weibo: 0,
          qq: 0
        },
        threeIdol: [],
        myMoments: [],
        active: 0,//tab
        showPopup: false,//弹窗
        list: ['屏蔽此动态', '屏蔽此人', '举报', '取消'],
        tabName: ['动态', '应援', '商品'],
        showAd: true, //是否显示广告
        picLargeArr: [], //大图的数组
        largeIndex: Number, //大图当前滑块的索引
        support: [],
        goods: []
      }
    },
    mounted() {
      let params = this.$queryParams(location.href);
      this.getUserInfo(params);
      this.getIdolList(params);
      this.getMoments(params);
      this.getLikeNum(params);
      this.getSupport(params);
      this.getGoods(params);
    },
    computed: {
      userHeader: function () {
        return {
          backgroundSize: 'cover',
          background: 'url(' + this.userInfo.headUrl + ')center no-repeat'
        }
      }
    },
    created() {
      document.title = '个人主页';
    },
    methods: {
      playVideo: function (index, event) {
        let parent = document.getElementsByClassName('item-sweetfans')[index];
        let videos = document.getElementsByTagName('video');
        for (let i = 0; i < videos.length; i ++) {
          videos[i].pause();
        }
        let v = parent.getElementsByTagName('video')[0];
        if (v.readyState >= 2) {
          v.play();
          parent.getElementsByClassName('load-layer')[0].classList.add('display');
        }
      },
      getUserInfo: function (params) {
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE1 + '/user_server/tu-user/getUserById',
          data: this.$qs.stringify({
            userId: params.userId,
            targetUserId: params.targetUserId,
            token: params.token
          })
        }).then((res) => {
          if (res !== null) {
            if (res.data.status === 1000) {
              this.userInfo = res.data.data;
            }
          }
        }).catch((err) => {
          console.warn(err);
        })
      },
      getIdolList: function (params) {
        //关注的爱豆
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE1 + '/user_server/singer/getSingerByUserId', // 爱豆列表
          data: this.$qs.stringify({
            userId: params.userId
          })
        }).then((res) => {
          if (res !== null) {
            if (res.data.status === 1000) {
              this.threeIdol = res.data.data.slice(0, 3);
            }
          }
        }).catch((err) => {
          console.warn(err);
        });
      },
      getLikeNum: function (params) {
        // 用户获赞数
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE2 + '/msgServer/msg/getReadByUserId',// 用户动态
          data: this.$qs.stringify({
            userId: params.userId,
            token: params.token
          })
        }).then((res) => {
          if (res !== null) {
            if (res.data.status === 1000) {
              this.likenum = res.data.data.likenum;
            }
          }
        }).catch((err) => {
          console.warn(err);
        })
      },
      getMoments: function (params) {
        // 用户动态
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE3 + '/dynamic/getDynamicPageByUserId',// 用户动态
          data: this.$qs.stringify({
            userId: params.userId,
            pageNum: 0,
            targetUserId: params.targetUserId
          })
        }).then((res) => {
          if (res !== null) {
            if (res.data.status === 1000) {
              this.myMoments = res.data.data;
              for (let i = 0; i < this.myMoments.length; i ++) {
                if (this.myMoments[i].notice && this.myMoments[i][0] !== null) {
                  if (this.myMoments[i].notice[0] === null) {
                    this.myMoments[i].notice = []
                  } else {
                    this.myMoments[i].title = this.$atSomebody(this.myMoments[i].notice, this.myMoments[i].title);
                  }
                }
                if (this.myMoments[i].location) {
                  this.myMoments[i].location = this.myMoments[i].location.split(',');
                }
                if (this.myMoments[i].url === null) {
                  this.myMoments[i].url = [];
                }
                if (this.myMoments[i].type === 3) {
                  let old = this.myMoments[i];
                  this.$set(old, 'vPosterImg', old.url[0] + '?vframe/jpg/offset/4/h/960');
                }
                if (this.myMoments[i].type === 4) {
                  let old = this.myMoments[i];
                  this.$set(old, 'vPosterImg', old.url[0] + '?vframe/jpg/offset/4/h/960')
                }
              }
            }
          }
        }).catch((err) => {
          console.warn(err);
        })
      },
      getSupport: function (params) {
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE4 + '/assistance/singerActivityPageByUserId',// 用户动态
          data: this.$qs.stringify({
            userid: params.targetUserId,
            pageNum: 0,
            pageSize: 10
          })
        }).then((res) => {
            if (res !== null) {
              if (res.data.status !== 1000) {
                return false;
              } else {
                this.support = res.data.data;
                for (let i = 0; i < this.support.list.length; i ++) {
                  if (this.support.list[i].moneyneed === 0) {
                    let item = this.support.list[i];
                    this.$set(item, 'percent', item.integralnow / item.integralneed);
                  }
                  if (this.support.list[i].integralneed === 0) {
                    let item = this.support.list[i];
                    this.$set(item, 'percent', item.moneynow / item.moneyneed); //正确姿势
                  }
                  if (this.support.list[i].endtime === 0) {
                    this.support.list[i].endtime = '长期';
                  }
                }
              }
            }
          }
        ).catch((err) => {
          console.warn(err);
        })
      },

      getGoods: function (params) {
        // 用户动态
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE4 + '/GoodsAPI/getGoodsByTeamUserId',//
          data: this.$qs.stringify({
            addUserId: params.targetUserId,
            pageNum: 1,
            type: 1,
            pageSize: 10
          })
        }).then((res) => {
          if (res !== null) {
            if (res.data.status === 1000) {
              this.goods = res.data.data;
            }
          }
        }).catch((err) => {
          console.warn(err);
        })
      },
      //是否显示广告
      isShowAd: function () {
        this.$toast('关闭成功');
        this.showAd = false;
      },

      changeActive: function () {
        // alert(this.active);
      },

      //查看大图
      viewPicture: function (i, d) {
        this.picLargeArr = d;
        ImagePreview({
          images: d,
          startPosition: i
        })
      },

      tipOffs: function (n) {
        this.showPopup = ! this.showPopup;
        if ((typeof n).toLowerCase() === 'string') {
          this.$toast(n);
        }
      }
    }
  }
</script>
<style scoped lang="scss">

    @mixin radius50 {
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        border-radius: 50%;
    }

    @mixin wh100 {
        width: 100%;
        height: 100%;
    }

    @mixin radius1 {
        -webkit-border-radius: .1rem;
        -moz-border-radius: .1rem;
        border-radius: .1rem;
    }

    @mixin radius-bom1 {
        -webkit-border-radius: 0 0 .1rem .1rem;
        -moz-border-radius: 0 0 .1rem .1rem;
        border-radius: 0 0 .1rem .1rem;
    }

    @mixin line2 {
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
    }

    $width93: 93.5%;

    .display {
        display: none;
    }

    .van-cell__title {
        text-align: center;
    }

    .van-tabs__wrap {
        display: none;
    }

    .van-tabs--line {
        /*padding: 0;*/
    }

